.ui.inverted.top.fixed.menu {
  background-image: linear-gradient(
    135deg,
    rgb(67, 39, 181) 0%,
    rgb(149, 128, 223) 69%,
    rgb(117, 84, 178) 89%
  ) !important;
  padding: 0 24px;
  position: relative;
  min-height: 60px;
}

h3 {
  margin-top: 0;
  margin-bottom: 0;
}

.vertical-line {
  border-left: 3px solid white;
  opacity: 0.16;
  height: 40px;
}

.ui.inverted.menu .active.item {
  background: transparent;
  color: #fff !important;
}

.ui.secondary.inverted.menu .active.item {
  background: transparent;
  color: #fff !important;
}
